import None from '../../public/noNumber'
import test from '../../../static/js/language'
  export default {
      name: "sucessInvit",
    data(){
        return{
          api:this.GLOBAL.baseURL,
          options:[
            {label:'待处理',value:'0'},
            {label:'已同意',value:'1'},
            {label:'已拒绝',value:'2'},
          ],
          value:'',
          sucessList:[],
          active_id:'',
          total:0,
          language:[],
          asdasdasd:'',
          index:1
        }
    },
    components:{
      None
    },
    watch:{
      value:function(){
        this.getList(1)
      }
    },
    methods:{
      timeNow(item){
        let a = ''
        for(let i = 0;i<item.slot.length;i++){
           a+= item.slot[i].begin_time + '—'+item.slot[i].end_time + ' ||  '
        }
        this.asdasdasd = a
      },
      open(item,index) {
        this.$confirm('您正在撤回对该志愿者的邀请, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
         this.returnInvit(item,index)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消撤回'
          });
        });
      },
      returnInvit(item,index){
        this.$http.post(this.api + '/InviteD',{
          token: localStorage.getItem('token'),
          invite_id: item.invite_id
        })
          .then(res=>{
            if(res.data.code == 1){
              this.sucessList.splice(index,1)
              this.$message({
                type: 'success',
                message: '撤回成功!'
              });
            }else if(res.data.code == 3){
              this.$http.post(this.api +'/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.returnInvit(item,index)
                })
            }else{
              alert(res.data.msg)
            }
          })
      },
      getList(val){
        this.$http.post(this.api + '/InviteListMy',{
          token: localStorage.getItem('token'),
          audit:this.value,
          activity_id: this.active_id,
          page:val
        })
          .then(res=>{
            if(res.data.code == 1){
              this.total = res.data.data.total
              this.sucessList = res.data.data.data
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getSucess(val)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
        getSucess(val){
          this.$http.post(this.api + '/InviteListMy',{
            token: localStorage.getItem('token'),
            activity_id: this.active_id,
            page:val
          })
            .then(res=>{
              if(res.data.code == 1){
                this.total = res.data.data.total
                this.sucessList = res.data.data.data
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.getSucess(val)
                  })
              }else{
                alert(res.data.msg)
              }
            })
        },
      handleCurrentChange(val){
        if(this.value){
          this.getList(val)
        }else{
          this.getSucess(val)
        }
      }
    },
    mounted(){
        this.getSucess(1)
    },
    created(){
      this.active_id = this.$route.query.activeId
      this.language = test()
    }
  }



